import React from 'react';
import { Container, Col, Carousel } from 'react-bootstrap';
import './style.css';

import landingA from '../../Assets/Images/landing1.jpg';
import landingB from '../../Assets/Images/landing2.jpg';
import landingC from '../../Assets/Images/landing3.jpg';

const Landing = () => {
    return (
        <div className="landing">
            <Carousel>
                <Carousel.Item>
                    <img
                        className="carousel_image d-block w-100"
                        src={landingA}
                        alt="First slide"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="carousel_image d-block w-100"
                        src={landingB}
                        alt="Second slide"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="carousel_image d-block w-100"
                        src={landingC}
                        alt="Third slide"
                    />
                </Carousel.Item>
            </Carousel>
            <Col md={6} className="landing_info">
                Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.
            </Col>
        </div >
    );
}

const Featuring = () => {
    return (
        <Container>

        </Container>
    )
}

const Home = () => {
    return (
        <div className="home">
            <Landing />
            <div className="section_divider" />
            <Featuring />
        </div>
    );
};

export default Home;